import React from 'react';

import axios from 'axios';
import ReactHtml from 'raw-html-react';
export default class PersonList extends React.Component {
  state = {
    persons: []
  }

  componentDidMount() {
    axios.get(`http://rgde.riset.pcr.ac.id/incubiz/berita/json`,{headers: { 'content-type': 'application/json' }})
      .then(res => {
        const persons = res.data.data;
        console.log(persons);
        this.setState({ persons });
      })
  }

  render() {
    return (
        <div>
            <section id="team" class="section-padding bg-gray">
      <div class="container">
        <div class="section-header text-center">          
          <h2 class="section-title wow fadeInDown" data-wow-delay="0.3s">Meet our team</h2>
          <div class="shape wow fadeInDown" data-wow-delay="0.3s"></div>
        </div>
        <div class="row">
        { this.state.persons.map(person => 
        <div class="col-lg-6 col-md-12 col-xs-12">
            
        <div class="team-item wow fadeInRight" data-wow-delay="0.2s">
          <div class="team-img">
            <img class="img-fluid" src="assets/img/team/team-01.png" alt=""/>
          </div>
          <div class="contetn">
            <div class="info-text">
              <h3><a href="#">{person.nama_berita}</a></h3>
              <p>{person.jenis_berita}</p>
            </div>
            <p>
            <ReactHtml html={person.keterangan.substring(0, 100)} />
            </p>
            <ul class="social-icons">
              <li><a href="#"><i class="lni-facebook-filled" aria-hidden="true"></i></a></li>
              <li><a href="#"><i class="lni-twitter-filled" aria-hidden="true"></i></a></li>
              <li><a href="#"><i class="lni-instagram-filled" aria-hidden="true"></i></a></li>
            </ul>
          </div>
        </div>
        
      </div>
       )}
          
        </div>
      </div>
    </section>
        </div>
      
    )
  }
}